@use '../../../assets/partials/colors';

.director-display{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.director-movie-display-card{
    width: 350px;
    height: 475px;
    margin: 1rem;
    background-color: colors.$medium-grey;
    border-radius: 3px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.75);
    overflow: hidden;
    position: relative;
}

.director-movie-display-info{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
}


.director-movie-display-image{
    width: 100%;
    height: 75%;
    object-fit: cover;
}

.director-movie-display-summary{
    background-color: colors.$dark-grey;
    position: absolute;
    padding: 1rem;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateY(100%);
    transition: transform 0.4s ease-in-out;
}

.director-movie-display-card:hover .director-movie-display-summary{
    transform: translateY(0%);
}



